.Individual{
    display: flex;
    background-color: #fff;
    width: 90%;
   
    margin:50px auto;
    border-radius: 30px;
    box-shadow: 5px 3px 7px gray;
}

.leftData{
    padding: 20px;
    border-radius: inherit;
    width:30% ;
    background-color: #ffffff;
    
}

.rightData{
    padding: 20px;
    border-top-right-radius: inherit;
    border-bottom-right-radius: inherit;
    box-sizing: border-box;
    margin: 0px;
    width: 70%;
    background-color: #f6f6f8;
}


.input{
    width: 80%;
    border: none;
    padding: 10px;
    font-size: 20px;
}

.input:focus{
    outline: none;
}

.button{
    border: none;
    background-color: white;
}

.button:focus{
    border: none;
    border-color: none;
    outline: none;
}

.search{
    box-sizing: border-box;
    width: 100%;
    margin: 10px 2px;
}

.currentImg{
    box-sizing: border-box;
    width: 100%;
}

.currentImg>img{
    width: 80%;
    display:block;
    margin: auto;
    
}

hr{
    width: 70%;
    background-color:gray;
}

.temptime{
    width: 100%;
}

.temptime>h1{
    font-weight: normal;
    font-size: 50px;
}

.temptime>h1, .temptime>h3{
    display: block;
    width: 50%;
    
    margin: 20px auto;
}

.btnDisplay{
    position: relative;
    top: 20px;
    text-align: center;
    width: 80%;
    margin: 0 auto;
}





/*second*/
.header{
    display: flex;
    justify-content: space-between;
}



.weekdiv{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.weekdiv>div{
    box-sizing: border-box;
    
    border-radius: 20px;
    background-color: white;
    margin: 5px;
    height: 200px;
    width:30% ;
}

.weekdiv>div>p{

    padding: 20px;
    text-align: center;
    
}

.weekdiv>div>h1{
    text-align: center;
    color: yellowgreen;
    font-weight: normal;
}

.weekdiv>div>h3{
    font-weight: normal;
    text-align: center;
    color: yellowgreen;
    font-size: 1.45em;
}


.weekfirst{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}
